import { defineStore } from "pinia";
import axios from "axios";
import { config } from "./utils/config";
import { telegramStore } from "@/store/telegram";
import { checkResponseSuccess } from "@/store/utils/apiUtils";

export const friendsStore = defineStore("friends", {
  state: () => ({
    inviteLink: "null as null | string",
    friends: [
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
      {
        name: "Имя",
        boost: 10,
        total: 20,
      },
    ],
  }),

  actions: {
    async fetchFriends() {
      try {
        const webAppData = telegramStore().ensureWebAppData();

        const response = await axios.post(
          `${config.backendURL}/api/cards/get-karma`,
          webAppData
        );

        checkResponseSuccess(response);

        this.friends = response.data.data.friends;
      } catch (error) {
        console.error("Ошибка при получении друзей:", error);
        throw new Error("Server error when getting friends list");
      }
    },

    async ensureInviteLink() {
      return this.inviteLink
    },

    async requestInviteLink(): Promise<string> {
      const webAppData = telegramStore().ensureWebAppData();

      const response = await axios.post(
          `${config.backendURL}/api/request-invite-link`,
          webAppData
      );

      checkResponseSuccess(response);

      return response.data.data.inviteLink;
    },
  },

  getters: {},
});
